    // }
    /**  config */
    public ready = async (config: WxType.WxConfigType): Promise<any> => {
        return new Promise((resolve) => {
            /** config */
            wx.config(config);
            /** ready */
            wx.ready(() => {
                /**
                 * configreadyconfig
                 * config
                 * ready
                 * ready
                 */
                resolve(true);
            });
            /** error */
            wx.error((res) => {
                /**
                 * configerror
                 * configdebug
                 * resSPA
                 */
                resolve(false);
            });
        });
    }

    /** agentConfig */
    public agentConfig = async (config: WxType.WxAgentConfigType): Promise<any> => {
        return new Promise((resolve) => {
            alert(wx.agentConfig);
            wx.ready(() => {
                wx.agentConfig({
                    corpid: config.corpid,
                    agentid: config.agentid,
                    timestamp: config.timestamp,
                    nonceStr: config.nonceStr,
                    signature: config.signature,
                    jsApiList: config.jsApiList,
                    success: function (res) {
                        alert(`${JSON.stringify(res)}`);
                        resolve(true);
                    },
                    fail: function (res) {
                        if (res.errMsg.indexOf('function not exist') > -1) {
                            alert('')
                        }
                        resolve(false);
                    }
                });
            });
            wx.error((res) => {
                resolve(false);
            });

        });
    }

    /**  */
    public getLocation = async (): Promise<any> => {
        return new Promise(async (resolve) => {
            const result = { lat: 0, lng: 0 };
            const config: WxType.WxConfigType = await this._wxService.getJssdkConfig();
            const isOk: boolean = await this.ready(config);
            if (isOk) {
                wx.getLocation({
                    type: 'wgs84', // wgs84gpsopenLocation'gcj02'
                    success: function (res: any) {
                        let latitude = res.latitude; // 90 ~ -90
                        let longitude = res.longitude; // 180 ~ -180
                        // let speed = res.speed; // /
                        // let accuracy = res.accuracy; // 
                        result.lat = latitude;
                        result.lng = longitude;
                        resolve(result);
                    }
                });
            } else {
                resolve(result);
            }
        });
    }

    /**  */
    public onHistoryBack = async (): Promise<any> => {
        
        return new Promise(async (resolve) => {
            let result = false;
            const config: WxType.WxConfigType = await this._wxService.getJssdkConfig();
            const isOk: boolean = await this.ready(config);
            if (isOk) {
                wx.onHistoryBack(function(){
                    wx.closeWindow();
                    result = true;
                    return result;
                });
            }
            resolve(result);
        });
    }


    /**  */
    public openLocation = async () => {

    }



