import { FormGroup } from "@angular/forms";

/** sheetlink get  */
export interface SheetlinkGetData {
    /**  */
    Type: string,
    /** workFlowId */
    DoWorkflow: string,
}

/** sheetlink post  */
export interface SheetlinkPostData {
    /** xxx id */
    choseData: string,
    /**  */
    currentData: {
        /**  */
        main: any,
        /**  */
        sub: any,
    },
}


/** sheetlink   */
export interface SheetlinkFetchData {
    /**  */
    Main?: { [key: string]: SelectedDataType },
    /**  */
    relation?: {
        /**  */
        [tableName: string]: {
            /**  */
            NoPrompt: NoPromptEnum,
            /**  */
            data: Array<SheetlinkChildData>,
        },
    },
}

/** sheetlink  */
export interface SheetlinkChildData {
    /**  */
    [fieldName: string]: {
        value: any,
        displayValue: any,
    }
}


/** sheetlink   SelectedData */
export interface SelectedDataType {
    /**   */
    key: string;
    /**  */
    value: any;
    /**  */
    displayValue?: string;
}

/** sheetlink  SheetlinkParams */
export interface SheetlinkParamsType {
    workFlowId?: string; // ID
    moduleName: string; // 
    /**  sheetlink ajax  */
    nocascadesheetlink?: boolean,
    /**  */
    editData: any,
    /**  */
    mainForm: FormGroup,
}

/**  */
export enum NoPromptEnum {
    /**  */
    confirm = 0,
    /**  */
    clearAdd = 1,
    /**  */
    concat = 2,
}


