import { Input } from '@angular/core';


export class ListItemClass {

    /**  */
    @Input() public name: string = "";
    /**  */
    @Input() public value: any = "";
    /**  */
    @Input() public operate: boolean = true;
    /** value  */
    @Input() public setStyle: any = {};

    public labelMaxLen : number = 7;

    constructor() { }

}


